import React from 'react';
import logo from './logo.svg';
import './App.css';


function App() {
  let titleStyle = {fontStyle:"italic"};
  return (
    <div >
      <h4>Simple Demo projects</h4>
      <h8 style={titleStyle}>for quickly starting 'React'</h8>
      <ul>
      <li> a Calculator</li>
      <li> a Pixel Editor</li>
      <li> a Quote Gallery</li>
      </ul>
    </div>
  );
}

export default App;
